import React, { useState, useEffect, useCallback } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useSnackbar } from 'notistack';
import {
  Box,
  Button,
  Card,
  CardContent,
  Grid,
  TextField,
  makeStyles
} from '@material-ui/core';
import { KeyboardDatePicker } from '@material-ui/pickers';
import wait from 'src/utils/wait';
import {random_number} from 'src/utils/stringutil'
import useIsMountedRef from 'src/hooks/useIsMountedRef';
import { useHistory } from 'react-router-dom';
import { addStudent } from '../../../api/students';
import { listAllClasses } from '../../../api/classes';
import useAuth from 'src/hooks/useAuth';

const useStyles = makeStyles(() => ({
  root: {}
}));

const genders = [
  {
    id: 'male',
    name: 'Nam'
  },
  {
    id: 'female',
    name: 'Nữ'
  }
];
const StudentCreateForm = ({ className, ...rest }) => {
  const { user } = useAuth();
  const history = useHistory();
  const schoolid = localStorage.getItem('schoolid');
  const classes = useStyles();
  const [sclass, setSClass] = useState('');
  const [sclasses, setClasses] = useState([]);
  const isMountedRef = useIsMountedRef();
  const getClasses = useCallback(async () => {
    try {
      const sclasses = await listAllClasses(schoolid);
      console.log(sclasses);
      if (isMountedRef.current) {
        setClasses(sclasses);
        console.log('class loaded');
        setSClass(sclasses[0].id);
      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);

  useEffect(() => {
    getClasses();
  }, [getClasses]);

  const handleClassChange = event => {
    event.persist();
    console.log(event.target.value)
    setSClass(event.target.value);
  };
  const { enqueueSnackbar } = useSnackbar();
  return (
    <Formik
      initialValues={{
        name: '',
        birthdate: new Date(),
        parent_name: '',
        parent_phone: '',
        submit: null
      }}
      validationSchema={Yup.object().shape({
        name: Yup.string()
          .max(75)
          .required('Bắt buộc nhập tên'),
        birthdate: Yup.date(),
        parent_name: Yup.string().max(75),
        parent_phone: Yup.string().max(15)
      })}
      onSubmit={async (
        values,
        { resetForm, setErrors, setStatus, setSubmitting }
      ) => {
        try {
          values.school_id=schoolid;
          // values.school_name=school.name;
          let c = {};
          console.log(sclass);
          c.id=sclass
          for await (let clas of sclasses){
            if(clas.id==c.id){
              c.name=clas.name;
            }
          }
          values.class = c;
          values.displayName = values.name
          let fullName = values.name.split(' ');
          console.log(fullName.length)
            values.firstname = fullName[0];
            values.lastname = fullName[fullName.length - 1];
          values.password = "abcd" + await random_number(4);
          values.status=0;
          console.log(values);
          addStudent(user, values);
          // NOTE: Make API request
          await wait(500);
          history.push('/school/management/students');
        } catch (err) {
          console.error(err);
          setStatus({ success: false });
          setErrors({ submit: err.message });
          setSubmitting(false);
        }
      }}
    >
      {({
        errors,
        handleBlur,
        handleChange,
        handleSubmit,
        isSubmitting,
        setFieldValue,
        setFieldTouched,
        touched,
        values
      }) => (
        <form
          className={clsx(classes.root, className)}
          onSubmit={handleSubmit}
          {...rest}
        >
          <Card>
            <CardContent>
              <Grid container spacing={3}>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.name && errors.name)}
                    fullWidth
                    helperText={touched.name && errors.name}
                    label="Tên học sinh"
                    name="name"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    required
                    value={values.name}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    label="Lớp"
                    name="sclass"
                    onChange={handleClassChange}
                    select
                    SelectProps={{ native: true }}
                    variant="outlined"
                    value={sclass}
                  >
                    {sclasses.map(s => (
                      <option key={s.id} value={s.id}>
                        {s.name}
                      </option>
                    ))}
                  </TextField>
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    fullWidth
                    label="Giới tính"
                    name="gender"
                    onChange={handleChange}
                    select
                    SelectProps={{ native: true }}
                    value={values.gender}
                    variant="outlined"
                  >
                    {genders.map(gender => (
                      <option key={gender.id} value={gender.id}>
                        {gender.name}
                      </option>
                    ))}
                  </TextField>
                </Grid>
                <Grid item md={6} xs={12}>
                  <KeyboardDatePicker
                    className={classes.datePicker}
                    label="Ngày sinh (ngày/tháng/năm)"
                    format="DD/MM/YYYY"
                    name="birthdate"
                    inputVariant="outlined"
                    value={values.birthdate}
                    onBlur={() => setFieldTouched('birthdate')}
                    onClose={() => setFieldTouched('birthdate')}
                    onAccept={() => setFieldTouched('birthdate')}
                    onChange={date => setFieldValue('birthdate', date)}
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.parent_name && errors.parent_name)}
                    fullWidth
                    helperText={touched.parent_name && errors.parent_name}
                    label="Tên phụ huynh"
                    name="parent_name"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.parent_name}
                    variant="outlined"
                  />
                </Grid>
                <Grid item md={6} xs={12}>
                  <TextField
                    error={Boolean(touched.parent_phone && errors.parent_phone)}
                    fullWidth
                    helperText={touched.parent_phone && errors.parent_phone}
                    label="Số điện thoại phụ huynh"
                    name="parent_phone"
                    onBlur={handleBlur}
                    onChange={handleChange}
                    value={values.parent_phone}
                    variant="outlined"
                  />
                </Grid>
                <Grid item />
              </Grid>
              <Box mt={2}>
                <Button
                  variant="contained"
                  color="secondary"
                  type="submit"
                  disabled={isSubmitting}
                >
                  Lưu
                </Button>
              </Box>
            </CardContent>
          </Card>
        </form>
      )}
    </Formik>
  );
};

StudentCreateForm.propTypes = {
  className: PropTypes.string
};

export default StudentCreateForm;
